<template>
    <div>
        <a-button size="small" type="primary"
                  @click="dialogVisible=true">
            上传图片
        </a-button>
        <a-modal v-model="dialogVisible"
                 :destroyOnClose="true"
                 @ok="handleSubmit" okText="确认上传" cancelText="取消">
            <UploadImages v-model="fileList"/>
        </a-modal>
    </div>
</template>

<script>
    import UploadImages from '@/components/upload/UploadImages'

    export default {
        name: 'EditorSlideUpload',
        components: {UploadImages},
        data() {
            return {
                dialogVisible: false,
                fileList: []
            }
        },
        methods: {
            handleSubmit() {
                this.$emit('successCBK', this.fileList);
                this.dialogVisible = false
            },
        }
    }
</script>

